<template>
	<div class="active-btn">
		<img :src="btnImg" @click="submit" />
	</div>
</template>
<style lang="less" scoped>
	.active-btn{
		width: 100%;
		margin:25px 0;
		img{
			height:60px;
		}
	}
</style>
<script>
	export default{
	  	props: ['btnImg', 'submit']
	}
</script>


