<template>
	<li>
    	<div class="item-content">
      		<div class="item-media">
      			<img :src="item.head_image">
      			<i class="icon-vip" v-if='item.is_authentication != "0"'></i>
  			</div>
      		<div class="item-inner">
        		<div class="item-title-row">
          			<div class="item-title">{{item.nick_name}}</div>
        		</div>
       	 		<div class="item-text">
              <template v-if="item.to_nick_name">回复<span class="nick-name">{{item.to_nick_name}}</span>：{{item.content}}</template>
              <template v-else>{{item.content}}</template>
            </div>
       	 		<div class="item-after">{{item.left_time}}</div>
      		</div>
      		
    	</div>
  	</li>
</template>
<script>
	export default{
		props: ['item']
	}
</script>


