<template>
	<div class="container">
		<!-- 写真轮播 -->
		<swiper-images>
			<swiper-images-item v-for="item in user_info.images" :item="item" key></swiper-images-item>
		</swiper-images>
		<!-- 写真简介 -->
    	<div class="brief">
    		<div class="avatar">
				<img :src="user_info.head_image" />
				<i class="icon-vip" v-if='user_info.is_authentication != "0"'></i>
			</div>
			<div class="text">
				<span>{{user_info.nick_name}}</span>
				<p>{{user_info.content}}</p>
				<div class="digg"><i class="icon-zan"></i>{{digg_num}}</div>
			</div>
    	</div>
    	<!-- 评论列表 -->
    	<div class="comment-block">
	    	<template v-if="comment_list.length">
		    	<comment-list>
		    		<comment-list-item v-for="item in comment_list" :item="item" key></comment-list-item>
		    	</comment-list>
	    	</template>
	    	<template v-else>
	    		<null-data text="暂无评论列表"></null-data>
	    	</template>
    	</div>
    	<!-- 下载按钮 -->
    	<div class="btn-block">
    		<mt-button type="danger" @click="down_load">下载APP</mt-button>
    	</div>
	</div>
</template>
<script>
	import api from '../config/api'
 	import Swiper from '../../static/js/swiper/swiper-3.4.2.min.js'
 	import CommentList from '../components/CommentList'
 	import CommentListItem from '../components/CommentListItem'
 	import SwiperImages from '../components/SwiperImages'
 	import SwiperImagesItem from '../components/SwiperImagesItem'
 	import nullData from '../components/nullData'
	require('../../static/js/swiper/swiper-3.4.2.min.css')
	import { Toast, Indicator, MessageBox } from 'mint-ui'
	export default {
   		components: {
   			CommentList,
   			CommentListItem,
   			SwiperImages,
   			SwiperImagesItem,
   			nullData
   		},
   		data(){
   			return {
   				comment_list: [],
   				user_info: {},
   				digg_num: 0,
   				app_down_url: ''
   			}
   		},
   		created(){
   			this.get();
   		},
   		mounted () {
   		},
   		methods:{
   			get(){
				// 抓取数据
				var self = this;
				self.axios.get(api.get_photoDetail(),{
			    	params: {
						"weibo_id": self.$route.query.weibo_id
			    	}
			  	})
	        	.then(res => {
		    		console.log(res.data);
		    		let result = res.data;
		    		if(result.status == 1){
		    			self.comment_list = result.comment_list;
		    			self.user_info = result.info;
		    			self.digg_num = result.digg_list && result.digg_list.length;
		    			self.app_down_url = result.app_down_url;
	                }
	                else{
	                	MessageBox(result.error);
	                }
	                self.$nextTick(function(){
	                	var mySwiper = new Swiper('.swiper-container', {
			       			direction: 'horizontal',
			       			loop: true,
			       			pagination: '.swiper-pagination',
			       			nextButton: '.swiper-button-next',
			       			prevButton: '.swiper-button-prev',
			       			paginationType: 'fraction',
			       			autoplay: 3000
			     		})
	                });

		        })
		        .catch(err => console.log(err))
   			},
   			down_load(){
   				console.log('下载app');
   				this.app_down_url && (location.href = this.app_down_url);
   			}
   		}
 	}
</script>
<style lang="less" scoped>
	@import "../assets/css/variable.less";
	.brief{
		display: flex;
		padding: 10px;
		background: #fff;
		.avatar{
			position: relative;
			width: 2rem;
			height: 2rem;
			margin-right: 0.5rem;
			display: inline-table;
			img{
				width: 100%;
				height: 100%;
				overflow: hidden;
				border-radius: 1rem;
			}
			.icon-vip{
				position: absolute;
				bottom: 0;
				right: 0;
				background-image:url(../assets/images/icon-vip.png);
				background-repeat: no-repeat;
				background-size: cover;
				width: 0.6rem;
				height: 0.6rem;
				display: block;
				z-index: 999;
			}
		}
		.text{
			position: relative;
			width: 100%;
			p{
				margin-top:0.25rem;
				color: @fc-light;
				font-size: @fs-smaller;
			}
			.digg{
				display: flex;
				justify-content: center;
				align-items: center;
				font-size: @fs-small;
				color: @color-theme;
				.icon-zan{
					width: 0.55rem;
					height: 0.525rem;
					display:block;
					background-image:url(../assets/images/icon-zan.png);
					background-repeat: no-repeat;
					background-size: contain;
					margin-right:0.25rem;
				}
				position: absolute;
				top: 0;
				right: 0.5rem;
			}
		}
	}
	.comment-block{
		margin-top: 0.5rem;
		background-color:#fff;
	}
	.btn-block{
		padding: 15px;
		.mint-button--normal{
			width: 100%;
		}
	}
</style>


