<template>
	<div class="active-container">
		<div class="main">
			<img src="../assets/images/img-activeTwo-1.jpg" />
			<active-text :avatar="from_head_image" :nickName="from_nick_name" :text="text"></active-text>
			<active-btn :btnImg="btnImg" :submit="download"></active-btn>
		</div>
	</div>
</template>
<style>
	img{ max-width:100% }
</style>
<script type="text/javascript">
	import api from '../config/api';
	import activeText from '../components/activeText.vue'
	import activeBtn from '../components/activeBtn.vue'
	export default{
        components: {
        	activeText,
        	activeBtn
	  	},
		data(){
			return {
				scrollWatch:true,
				from_head_image: '',
				from_nick_name: '',
                app_name: '',
				app_down_url: '',
				btnImg: require('@/assets/images/btn_download.png')
			}
		},
		created(){
			this.get();
		},
		mounted: function() {
			$(window).scrollTop(0);
		    $(window).on('scroll', () => {
		      	if (this.scrollWatch) {
	           		//your code here
        		}
		    });
        },
     	methods: {
     		get(){
     			// 抓取数据
				var self = this;
				self.axios.get(api.distributionInitRegister(),{
			    	params: {
			      		"test": 1,
						"user_id": self.$route.query.user_id
			    	}
			  	})
	        	.then(res => {
		    		console.log(res.data);
		    		var result = res.data;
		    		if(result.status == 1){
                        [this.from_head_image, this.from_nick_name, this.app_down_url, this.app_name] = [result.from_head_image, result.from_nick_name, result.app_down_url, result.app_name];
	                }
	                else{
	                	MessageBox(result.error);
	                }

		        })
		        .catch(err => console.log(err))
     		},
     		download(){
     			location.href = this.app_down_url;
     		}
        },
        destroyed () {
		    this.scrollWatch = false;
	  	},
        computed: {
            text(){
                return  `让我们一起在${this.app_name}上轻松的挣红包、愉快的玩耍吧！！`
            }
        }
	}
</script>


