<template>
	<div class="null-data">
		<img src="../assets/images/null-data.png">
		<span v-text="null_text"></span>
	</div>
</template>
<style lang="less" scoped>
	@import (once) "../assets/css/variable.less";
	.null-data{
		display: flex;
		justify-content:center;
		align-items: center;
		flex-direction:column;
		padding:10px;
		img{
			width:200px;
		}
		span{
			font-size: @fs-small;
			color: @fc-lighter;
		}
	}
</style>
<script>
	export default{
		props: ['text'],
		data(){
			return {
				null_text: this.text || '暂无数据'
			}
		}
	}
</script>


